import * as React from 'react';
import { SortableList } from 'oned-react';
import { Icon } from 'antd';

class SortableListDemo2 extends React.Component<any, any> {
  state = {
    items: ['pane1', 'pane2', 'pane3', 'pane4', 'pane5', 'pane6'],
  };

  render() {
    const items = this.state.items.map((val, index) => (
      <div key={val} data-id={val} style={{ border: '1px solid #dedede', marginBottom: 8, paddingLeft: 10 }}>
        <Icon type="drag" className="move" style={{ marginRight: 10, cursor: 'pointer' }} />
        {val}
      </div>
    ));

    return (
      <div>
        <SortableList
          options={{ handle: '.move', animation: 150 }}
          tagName="div"
          onChange={(order, sortable, evt) => {
            this.setState({ items: order });
          }}>
          {items}
        </SortableList>
      </div>
    );
  }
}

export default <SortableListDemo2 />;

