import * as React from 'react';
import { SortableList } from 'oned-react';
import { Button } from 'antd';

class SortableListDemo1 extends React.Component<any, any> {
  state = {
    items: ['pane1', 'pane2', 'pane3', 'pane4', 'pane5', 'pane6'],
  };

  render() {
    const items = this.state.items.map((val, index) => (
      <li key={val} data-id={val} style={{ background: '#fff', marginBottom: 6, paddingLeft: 10 }}>
        {val}
      </li>
    ));

    return (
      <div style={{ background: '#f8f8fa', padding: 10 }}>
        <SortableList
          options={{ animation: 150 }}
          tagName="ul"
          onChange={(order, sortable, evt) => {
            this.setState({ items: order });
          }}>
          {items}
        </SortableList>
        <Button
          type="primary"
          onClick={() => {
            this.setState({ items: this.state.items.concat(`pane${this.state.items.length + 1}`) });
          }}>
          新增
        </Button>
      </div>
    );
  }
}

export default <SortableListDemo1 />;

